<template>
	<div class="swapper" ref="swapper">
		<div class="content">
			<slot></slot>
		</div>
	</div>	
</template>

<script>
	import BScroll from 'better-scroll'
	export default{
		name: 'Scroll',
		data() {
			return {
				scroll: null
			}
		},
		props: {
			probeType: {
				type: Number,
				default: 0
			},
			pullUpLoad: {
				type: Boolean,
				default: false
			}
		},
		mounted() {
			this.scroll = new BScroll(this.$refs.swapper, {
                // probeType: 3,  //
                probeType: this.probeType,
                // pullUpLoad: true,
                // 
                pullUpLoad: this.pullUpLoad,
                observeDOM: true, //
                click: true
			});
			//
			this.scroll.on('scroll',(position) => {
				// console.log(position);
				this.$emit('scrollPosition', position)
			});
			//scroll
			if(this.pullUpLoad) {
				this.scroll.on('pullingUp',() => {
					this.$emit('loadMore')
				})
			}
		},
		methods:{
			scrollTo(x,y,time) {
				this.scroll && this.scroll.scrollTo(x,y,time); //scrollToscrollscrollTo
			},
			finishPullUp() {
				this.scroll && this.scroll.finishPullUp();
				//finishPullUpscrollfinishPullUp
			},
			refresh() {
				this.scroll && this.scroll.refresh();
			},
			getScrollY() {
				return this.scroll ? this.scroll.y : 0;
			}
		}
	}
</script>